<template lang="pug">
    div.ident-conent
        div.ident-text(v-text="investorSendInfo")
        mt-loadmore(
            :top-method="loadTop"
            ref="loadmore"
            @top-status-change="handleTopChange"
        )
            mint-loadmore-top(:topStatus="topStatus" slot="top")
            div.ident-item 
                div.ident-item-inner
                    h2.basic-title 基本资料
                    div.basic-inner  
                        p(v-text="`昵称：${userInfo.nick_name}`") 
                        p(v-if="userInfo.sex == 1") 性别：男
                        p(v-if="userInfo.sex == 2") 性别：女
                    h2.ident-title 认证消息
                    div.ident-inner
                        div 
                            span 认证类型
                            i.icon.iconfont &#xe616;
                            div.ident-type(v-if="user.is_authentication == 1 || user.is_authentication == 2")
                                span(v-if="user.authentication_type" v-text="user.authentication_type")
                                span(v-else) 请选择证件类型
                                i.icon.iconfont.drop-down &#xe631;
                            div.ident-type(v-if="user.is_authentication == 0 || user.is_authentication == 3" @click="showidentType()")
                                span(v-if="user.authentication_type" v-text="user.authentication_type")
                                span(v-else) 请选择证件类型
                                i.icon.iconfont.drop-down &#xe631;
                        div 
                            span 真实姓名
                            i.icon.iconfont &#xe616;
                            input(v-if="user.is_authentication == 1 || user.is_authentication == 2" v-model="Identname" disabled="disabled")
                            input(v-if="user.is_authentication == 0 || user.is_authentication == 3" placeholder="请输入您的真实姓名" v-model="Identname")
                        div 
                            span 联系方式
                            i.icon.iconfont &#xe616;
                            input(v-if="user.is_authentication == 1 || user.is_authentication == 2" type="number" v-model="IdentContact" disabled="disabled")
                            input(v-if="user.is_authentication == 0 || user.is_authentication == 3" type="number" placeholder="请输入您的联系方式" v-model="IdentContact")
                        div(v-if="isShowIdentifynumber == 1") 
                            span 身份证号
                            i.icon.iconfont &#xe616;
                            input(v-if="user.is_authentication == 1 || user.is_authentication == 2" v-model="IdentNumber" disabled="disabled")
                            input(v-if="user.is_authentication == 0 || user.is_authentication == 3" placeholder="请输入您的身份证号" v-model="IdentNumber")
                        div 身份认证
                    div.identity
                        div.identity-inner
                            div.identity-item(v-if="user.is_authentication == 1 || user.is_authentication == 2")
                                img(v-lazy="user.identify_positive_image")
                            div.identity-item(v-if="user.is_authentication == 0 || user.is_authentication == 3")
                                vue-core-image-upload.upload-img(
                                    class="btn btn-primary"
                                    @imageuploading="imageuploading"
                                    @imageuploaded="identifyPositiveImage"
                                    :max-file-size="5242880"
                                    :url="doUploadUrl"
                                    text=""
                                    inputOfFile="identify_positive_image"
                                    extensions="png,jpg,gif"
                                )
                                    img(v-if="user.identify_positive_image" v-lazy="user.identify_positive_image")
                                    i.icon.iconfont(v-else) &#xe63e;
                            div.identity-item-text 身份证正面照

                        div.identity-inner
                            div.identity-item(v-if="user.is_authentication == 1 || user.is_authentication == 2")
                                img(v-lazy="user.identify_nagative_image")
                            div.identity-item(v-if="user.is_authentication == 0 || user.is_authentication == 3")
                                vue-core-image-upload.upload-img(
                                    class="btn btn-primary"
                                    @imageuploading="imageuploading"
                                    @imageuploaded="identifyNagativeImage"
                                    :max-file-size="5242880"
                                    :url="doUploadUrl"
                                    text=""
                                    inputOfFile="identify_nagative_image"
                                    extensions="png,jpg,gif"
                                )
                                    img(v-if="user.identify_nagative_image" v-lazy="user.identify_nagative_image")
                                    i.icon.iconfont(v-else) &#xe63e;
                            div.identity-item-text 身份证反面照

                        div.identity-inner
                            div.identity-item(v-if="user.is_authentication == 1 || user.is_authentication == 2")
                                img(v-lazy="user.identify_hold_image")
                            div.identity-item(v-if="user.is_authentication == 0 || user.is_authentication == 3")
                                vue-core-image-upload.upload-img(
                                    class="btn btn-primary"
                                    @imageuploading="imageuploading"
                                    @imageuploaded="identifyHoldImage"
                                    :max-file-size="5242880"
                                    :url="doUploadUrl"
                                    text=""
                                    inputOfFile="identify_hold_image"
                                    extensions="png,jpg,gif"
                                )
                                    img(v-if="user.identify_hold_image" v-lazy="user.identify_hold_image")
                                    i.icon.iconfont(v-else) &#xe63e;
                            div.identity-item-text 手持身份证正面照

                div.ident-photo
                    img(v-lazy="identifyHoldExample")
                div.invite 公会邀请码
                    span (选填)
                    input(v-if="user.is_authentication == 1 || user.is_authentication == 2" type="text" v-model="societyCode" disabled="disabled")
                    input(v-if="user.is_authentication == 0 || user.is_authentication == 3" type="text" placeholder="请输入您的公会邀请码" v-model="societyCode")
                div.btn-group(v-if="user.is_authentication == 0 || user.is_authentication == 3")
                        div.btn-confirm(@click="submit") 提交
        //- - 验证类型列表
        mt-popup.popup(
            position="bottom"
            v-model="popupEmotionalVisible" 
        )       
            ul.authent_list
                li.list(v-for="item in listType" @click="getIdentType(item.name)")
                    div(v-text="item.name")
            div.cancel(@click="cancel") 取消
</template>
<script>
import { mapGetters, mapActions } from 'vuex'
import queryString from 'querystring'
import { Toast, Indicator } from 'mint-ui'
import mintLoadmoreTop from '@/components/mintLoadmoreTop'
import VueCoreImageUpload from 'vue-core-image-upload'
export default {
    name: 'userIdentIndex',
    components: {
        'vue-core-image-upload': VueCoreImageUpload,
        mintLoadmoreTop
    },
    beforeRouteEnter(to, from, next) {   // 路由进来之前
        next(vm => {
            vm.setFooterBarActiveName('userIdentIndex');
            vm.popupVisible = false;
            $(".page-content").scrollTop(0);
        });
    },
    data() {
        return {
            popupEmotionalVisible: false,
            topStatus: '',
            listType: '',
            user: '',
            Identname: '',
            IdentContact: '',
            societyCode: '',
            investorSendInfo: '',
            IdentNumber: '',
            isShowIdentifynumber: '',
            identifyHoldExample: ''
        }
    },
    created() {
        this.getData();
    },
    methods: {
        ...mapActions(['setFooterBarActiveName']),
        tabChange(name) {
            this.activeName = name;
        },
        getData(isRefresh) {
            let axiosUrl = process.env.NODE_ENV == 'development' ? '/gm/api/user_center_authent' : this.api.user_center_authent();
            this.axios.get(axiosUrl).then(res => {
                if (res.data.status == 1) {
                    this.user = res.data.user;
                    this.identifyHoldExample = res.data.identify_hold_example;
                    this.listType = res.data.authent_list;
                    this.Identname = res.data.user.authentication_name;
                    this.IdentContact = res.data.user.contact;
                    this.IdentNumber = res.data.user.identify_number;
                    this.societyCode = res.data.user.society_code;
                    this.investorSendInfo = res.data.investor_send_info
                    this.isShowIdentifynumber = res.data.is_show_identify_number;
                }
                else {
                    Toast(res.data.error || '请求出错');
                }
                this.$nextTick(() => {
                    isRefresh && this.$refs.loadmore.onTopLoaded();
                });
            })
                .catch(err => {
                    Toast(err || '请求失败');
                    isRefresh && this.$refs.loadmore.onTopLoaded();
                })
        },
        //- -下拉刷新页面
        loadTop() {
            this.getData(true);
        },
        handleTopChange(status) {
            this.topStatus = status;
        },
        //- -隐藏和显示认证类型
        showidentType() {
            this.popupEmotionalVisible = !this.popupEmotionalVisible;
        },
        //- -获取验证类型
        getIdentType(IdentType) {
            this.user.authentication_type = IdentType;
            this.popupEmotionalVisible = !this.popupEmotionalVisible;
        },
        //- -验证类型列表中取消
        cancel() {
            this.popupEmotionalVisible = !this.popupEmotionalVisible;
        },

        //- -图片上传的过程
        imageuploading() {
            Indicator.open('图片上传中...')
        },
        //- -正面照
        identifyPositiveImage(result) {
            Indicator.close()
            if (result.state == 200) {
                this.user.identify_positive_image = result.fullname;

            }
            else {
                Toast(result.message || '操作失败');
            }
        },
        //- -背面照
        identifyNagativeImage(result) {
            Indicator.close()
            if (result.state == 200) {
                this.user.identify_nagative_image = result.fullname;
            }
            else {
                Toast(result.message || '操作失败');
            }
        },
        //- -手持照
        identifyHoldImage(result) {
            Indicator.close()
            if (result.state == 200) {
                this.user.identify_hold_image = result.fullname;
            }
            else {
                Toast(result.message || '操作失败');
            }
        },

        check() {
            // 验证表单
            if (!this.user.authentication_type) {
                Toast('请选择证件类型');
                return false;
            }
            else if (!$.trim(this.Identname)) {
                Toast('请输入您的真实姓名');
                return false;
            }
            else if (!$.trim(this.IdentContact)) {
                Toast('请输入您的联系方式');
                return false;
            }
            else if (this.IdentContact.length != 11) {
                Toast('您输入的号码有误');
                return false;
            }
            else if (this.isShowIdentifynumber == 1 && !$.trim(this.IdentNumber)) {
                Toast('请输入您的身份证号')
                return false;
            }
            else if (!this.user.identify_positive_image) {
                Toast('请上传您的身份证正面照');
                return false;
            }
            else if (!this.user.identify_nagative_image) {
                Toast('请上传您的身份证反面照');
                return false;
            }
            else if (!this.user.identify_hold_image) {
                Toast('请上传您的身份证正面手持照');
                return false;
            }
            else {
                return true;
            }
        },

        //- -提交验证
        submit() {
            if (this.check()) {
                let axiosUrl = this.api.user_center_attestation();
                this.axios.post(axiosUrl, queryString.stringify({
                    authentication_type: this.user.authentication_type,
                    authentication_name: this.Identname,
                    contact: this.IdentContact,
                    identify_number: this.IdentNumber,
                    identify_positive_image: this.user.identify_positive_image,
                    identify_nagative_image: this.user.identify_nagative_image,
                    identify_hold_image: this.user.identify_hold_image,

                    society_code: this.societyCode
                })).then(res => {
                    if (res.data.status == 1) {
                        Toast('保存成功');
                        this.$router.push({ path: '/user/index' })
                    }
                    else {
                        Toast(res.data.error || '操作失败');
                    }
                })
                    .catch(err => {
                        Toast(err || '请求失败');
                    })
            }

        }
    },
    computed: {
        ...mapGetters({
            userInfo: 'getUserInfo'

        }),
        doUploadUrl() {
            return this.api.doUpload();
        }
    }
}
</script>





<style lang="less" scoped>
@import '../../../assets/css/variable.less';
.ident-conent {
    background-color: #fff;
    .ident-text {
        width: 100%;
        color: @fc-main;
        font-size: .6rem;
        padding: .3rem 0;
        background-color: #fbcfce;
        text-align: center;
        white-space: nowrap;
        position: fixed;
        top: 0;
        z-index: 1;
    }
    .ident-item {
        padding-left: .5rem;
        margin-top: 1.5rem;
        .basic-title {
            font-size: .85rem;
            color: @fc-main;
            font-weight: 400;
            border-bottom: 1px solid #e6eaea;
        }
        .basic-inner {
            padding: .5rem 0;
            p {
                color: @fc-darker;
                font-size: .75rem;
            }
        }
        .ident-title {
            font-size: .85rem;
            color: @fc-main;
            font-weight: 400;
            border-bottom: 1px solid #e6eaea;
        }
        .ident-inner {
            padding: .2rem .5rem 0 0;
            border-bottom: 1px solid #e6eaea;
            div {
                font-size: .75rem;
                color: @fc-main;
                height: 2rem;
                display: flex;
                align-items: center;
                span {
                    white-space: nowrap;
                }
                .iconfont {
                    font-size: .5rem;
                    color: #ff2116;
                    vertical-align: middle;
                    margin: 0 1rem 0 .5rem;
                }
                input {
                    height: 1.5rem;
                    width: 100%;
                    font-size: .6rem;
                    color: @fc-light;
                    padding-left: .5rem;
                    border-radius: .3rem;
                    outline: none;
                    border: 0;
                    -webkit-appearance: none;
                    border: 1px solid #dfdfdf;
                    font-family: "Helvetica Neue", Helvetica, sans-serif;
                    ime-mode: disabled;
                    background: #fff;
                }
                .ident-type {
                    height: 1.5rem;
                    line-height: 1.5rem;
                    width: 100%;
                    font-size: .6rem;
                    color: @fc-light;
                    position: relative;
                    border-radius: .3rem;
                    border: 1px solid #cccccc;
                    background: #efefef;
                    padding-left: .5rem;
                    .drop-down {
                        position: absolute;
                        height: 1.25rem;
                        width: 1.5rem;
                        line-height: 1.25rem;
                        text-align: center;
                        color: #8d8d8d;
                        margin: 0;
                        border-left: 1px solid #cccccc;
                        right: 0;
                        top: .1rem;
                    }
                }
            }
        }
        .identity {
            width: 100%;
            padding-top: .5rem;
            display: flex;
            justify-content: space-around;
            .identity-inner {
                width: 33.33%;
                text-align: center;
                padding-right: .5rem;
                .identity-item {
                    height: 5.5rem;
                    line-height: 5.5rem;
                    text-align: center;
                    overflow: hidden;
                    border: 1px dashed #e6eaea;
                    background-color: #fff;
                    img {
                        height: 100%;
                        width: 100%;
                        vertical-align: top;
                        border-radius: 0px;
                    }
                    .upload-img {
                        height: 100%;
                        width: 100%;
                        background-color: #fff;
                        border-radius: 0px;
                        .iconfont {
                            font-size: 1rem;
                            color: #bbbbbb;
                            line-height: 5.5rem;
                        }
                        img {
                            height: 100%;
                            width: 100%;
                            vertical-align: top;
                            border-radius: 0px;
                        }
                    }
                }
                .identity-item-text {
                    white-space: nowrap;
                    font-size: .65rem;
                    color: @fc-light;
                }
            }
        }
    }
    .ident-photo {
        text-align: center;
        padding: .75rem 0;
        img {
            width: 70%;
        }
    }
    .invite {
        padding: 0 .5rem .5rem;
        display: flex;
        align-items: center;
        white-space: nowrap;
        color: @fc-main;
        font-size: .75rem;
        span {
            color: @fc-light;
            font-size: .6rem;
            margin: 0 .3rem 0 .2rem;
        }
        input {
            height: 1.5rem;
            width: 100%;
            font-size: .6rem;
            color: @fc-light;
            padding-left: .5rem;
            border-radius: .3rem;
            outline: none;
            border: 0;
            -webkit-appearance: none;
            border: 1px solid #dfdfdf;
            font-family: "Helvetica Neue", Helvetica, sans-serif;
            background: #fff;
        }
    }
    .btn-group {
        margin-top: .6rem;
        padding: 0 .5rem 1rem;
        .btn-confirm {
            height: 2rem;
            text-align: center;
            line-height: 2rem;
            background-color: @color-theme;
            color: #fff;
            border-radius: 1rem;
        }
    }
    .popup {
        width: 100%;
        background: #e5e5e5;
        .authent_list {
            width: 100%;
            height: 100%;
            display: flex;
            flex-direction: column;
            align-items: center;
            .list {
                width: 100%;
                height: 2rem;
                line-height: 2rem;
                text-align: center;
                border-bottom: 1px solid #e5e5e5;
                background: #fff;
            }
        }
        .cancel {
            width: 100%;
            height: 2rem;
            line-height: 2rem;
            text-align: center;
            margin-top: .5rem;
            background: #fff;
        }
    }


    input::-webkit-input-placeholder {
        color: @fc-light;
    }
    input:-moz-placeholder {
        color: @fc-light;
    }
    input::-moz-placeholder {
        color: @fc-light;
    }
    input:-ms-input-placeholder {
        color: @fc-light;
    }
}
</style>




